import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import './registerServiceWorker';
import './assets/scss/main.scss';

import axios from 'axios';
axios.defaults.withCredentials = true;

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import Datetime from 'vue-datetime';
import 'vue-datetime/dist/vue-datetime.css';
Vue.use(Datetime);

import VueSocketio from 'vue-socket.io';
const SOCKET_URL =
  process.env.NODE_ENV !== 'development'
    ? '/'
    : '//localhost:3000';
console.log('SOCKETURL', SOCKET_URL)
Vue.use(VueSocketio, SOCKET_URL);

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

import FBSignInButton from 'vue-facebook-signin-button';
Vue.use(FBSignInButton);

import eventBusService, { PUSH_NOTIFICATION } from '@/services/eventBusService';


Vue.config.productionTip = false;
new Vue({
  router,
  store,
  render: h => h(App),
  created() {
    this.applyNotifications();
    this.displayNotification();
    eventBusService.$on(PUSH_NOTIFICATION, (pushNtf) => {
      console.log('eventBusOnPushNtf', pushNtf)
      this.displayNotification(pushNtf.title, pushNtf.options);
    });
  },
  methods: {
    applyNotifications() {
      Notification.requestPermission(function (status) {
        console.log('Notification permission status:', status);
      });
    },
    displayNotification(title = 'Hello world!', options = {
      body: 'Here is a notification body!',
      icon: 'data:image/png;base64,',
      vibrate: [100, 50, 100],
      data: {
        dateOfArrival: Date.now(),
        primaryKey: 1
      },
      actions: [
        {
          action: 'explore', title: 'Explore this new world',
          icon: 'data:image/jpeg;base64,'
        },
        {
          action: 'close', title: 'Close notification',
          icon: 'data:image/jpeg;base64,'
        },
      ]
    }) {
      if (Notification.permission == 'granted') {
        navigator.serviceWorker.getRegistration().then((reg) => {
          reg.showNotification(title, options);
        });
      }
    }
  }
}).$mount('#app');
